.app__aboutus {
  position: relative;
}

.app__aboutus-overlay {
  position: absolute;
  inset: 0;
}

.app__aboutus-overlay img {
  width: 391px;
  height: 415px;
  z-index: 0;
}

.app__aboutus-content {
  width: 100%;
  z-index: 2;
}

.app__aboutus-content_about {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  overflow: hidden;

  text-align: right;

  animation: from-left;
  animation-timeline: view();
  animation-range-start: cover;
  animation-range-end: 50vh;
}

.app__aboutus-content_knife {
  margin: 2rem 4rem;
}

.app__aboutus-content_knife img {
  height: 910px;

  animation: fade-in linear;
  animation-timeline: view();
  animation-range-start: cover;
  animation-range-end: 100vh;
}

.app__aboutus-content_team {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  overflow: hidden;

  text-align: left;

  animation: from-right;
  animation-timeline: view();
  animation-range-start: cover;
  animation-range-end: 50vh;
}

.app__aboutus-content_team p,
.app__aboutus-content_about p {
  margin: 2rem 0;
  color: var(--color-grey);
}

.custom__button{
  transition: 0.2s ease-in;
}

.custom__button:hover{
  background-color: var(--color-grey);
  color: var(--color-white);
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(1000px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes from-left {
  from {
    transform: translateX(-50px);
    opacity: 0;
    scale: 0.1;
  }

  to {
    transform: translateX(0);
    opacity: 1;
    scale: 1;
  }
}

@keyframes from-right {
  from {
    transform: translateX(50px);
    opacity: 0;
    scale: 0.1;
  }

  to {
    transform: translateX(0);
    opacity: 1;
    scale: 1;
  }
}

@media screen and (max-width: 2000px) {
  .app__aboutus-content_knife img {
    height: 1110px;
  }

  .app__aboutus-content_team p,
  .app__aboutus-content_about p {
    margin: 4rem 0;
  }
}

@media screen and (max-width: 900px) {
  .app__aboutus-content {
    flex-direction: column;
  }

  .app__aboutus-content_knife {
    margin: 4rem 0rem;
  }
}

@media screen and (max-width: 650px) {
  .app__aboutus-overlay img {
    width: 80%;
    height: 320px;
  }
}
